/* about.css */

body {
    margin: 0;
    padding: 0;
    background: #000;
  }
  
  .container {
    position: relative;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  
  img {
    width: 100%;
    height: auto;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
  
  .content {
    position: relative;
    z-index: 1;
    text-align: center;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 10px; /* Ajusta el padding para reducir el área del fondo transparente */
    border-radius: 10px;
    margin-top: 20px; /* Ajusta el margin-top para mover el fondo transparente hacia arriba */
    margin-bottom: 20px;
  }
  
  button {
    background-color: #3498db;
    color: #fff;
    padding: 10px 40px;
    font-size: 16px;
    margin-top: 20px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #2980b9;
  }
  
  h1 {
    font-size: 48px;
    margin-bottom: 20px;
    font-family: 'Press Start 2P', cursive;
    background-color: transparent;
    color: yellow;
  }
  
  h1:first-child {
    font-size: 64px;
  }
  
  h2 {
    font-size: 30px;
  }
  
  p {
    font-size: 15px;
  }
  
  /* Agregado estilo para la segunda imagen */
  img.presentation {
    width: 200px;
    height: auto;
    position: relative;
    margin: 0 auto;
    display: block;
  }
  